<template>
  <section class="home">
    <div class="header">
      <AppHeader />
      <TripFilter @setFilter="setFilter"/>
    </div>
    <div class="my-trips">
      <router-link to="/TripEdit">New Trip</router-link>
    </div>

    <div class="all-trips">
      <TripList :trips="getTrips" />
    </div>
  </section>
</template>


<script>
import TripList from "../components/TripList";
import TripFilter from "../components/TripFilter";
import AppHeader from "../components/Header";

export default {
  data() {
    return {};
  },
  created() {
    // this.loadOwnerTrips()
    this.setFilter();
  },
  computed: {
    getTrips() {
      return this.$store.getters.getTrips;
    }
  },
  methods: {
    // loadOwnerTrips(){

    // }
    setFilter(filter) {
      this.$store.dispatch({type: "loadTrips", filter});
    },
    
  },
  components: {
    TripList,
    TripFilter,
    AppHeader
  }
};
</script>


<style lang="scss" scoped>
.home {
  // height: 100vh;
  display: grid;
  grid-gap: 20px;
  grid-template-rows: 3fr 1fr 3fr;
  grid-template-columns: 1fr;
  grid-template-areas:
    "header"
    "my-trips"
    "all-trips";
}
.header {
  grid-area: header;
  background-image: url("../../public/img/hero.png");
  background-size: cover;
}
.my-trips {
  grid-area: my-trips;
  background-color: lightgreen;
}
.all-trips {
  grid-area: all-trips;
}
</style>