<template>
  <section class="nav" app>
    <div class="logo">
      <h2>Vamos</h2>
    </div>
    <div class="navbar">
      <button v-if="loggedInUser" @click="logout">Logout</button>
      <router-link v-if="!loggedInUser" to="/Signup">Signup</router-link>
      <router-link v-if="!loggedInUser" to="/Login">Login</router-link>
      <router-link to="/about">About</router-link>
      <router-link to="/">Home</router-link>
      <div v-if="loggedInUser"><img @click="goUserProfile" class="user-img" :src="loggedInUser.imgUrl"></div>
    </div>
  </section>
</template>



<script>
export default {
  data() {
    return {};
  },
  created() {},
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch({ type: "logout" });
    },
    goUserProfile(){
      this.$router.push(`/UserProfile/${this.loggedInUser._id}`)
    }
  }
};
</script>



<style lang="scss" scoped>
.nav {
  display: flex;
  justify-content: space-between;
  max-width: 90%;
  margin: 0 auto;
  background: none;
}
.logo,
.navbar {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo {
  font-family: Cabin;
  font-style: normal;
  font-weight: bold;
  font-size: 2rem;
  line-height: 39px;
}
.navbar a {
  cursor: pointer;
  margin-left: 20px;
}
a {
}
.user-img{
  cursor: pointer;
  width: 30px;
  margin-left: 20px;
  border-radius: 50%;
}
</style>
