<template>
  <section class="trip-edit">
    <AppHeader />
    <h2 v-if="trip._id">Edit</h2>
    <h2 v-else>New Trip</h2>
    <form @submit.prevent="save">
      <div>
        <label>
          Title
          <input type="text" v-model="trip.title" placeholder="Title" />
        </label>
      </div>
      <div>
        <label>
          Destination
          <input type="text" v-model="trip.destination" placeholder="Destination" />
        </label>
      </div>
      <div>
        <label>
          Start Date
          <input type="date" v-model="trip.start" />
        </label>
      </div>
      <div>
        <label>
          End Date
          <input type="date" v-model="trip.end" />
        </label>
      </div>
      <div>
        <label>
          Description
          <textarea v-model="trip.desc" placeholder="Description" cols="30" rows="10"></textarea>
        </label>
      </div>
      <div>
        <label>
          Meeting Point
          <input type="text" v-model="trip.meetingPoint" placeholder="Meeting Point" />
        </label>
      </div>
      <div>
        <label>
          Meeting Time
          <input type="time" v-model="trip.meetingTime" placeholder="Meeting Time" />
        </label>
      </div>
      <div>
        <label>
          Max Participants
          <input type="number" v-model="trip.participantCap" min="2" />
        </label>
      </div>
      <div>
        <label>
          Upload Image
          <input type="file" />
        </label>
      </div>
      <button>Save</button>
    </form>
  </section>
</template>


<script>
import AppHeader from "../components/Header";
export default {
  data() {
    return {
      trip: {
        title: "",
        desc: "",
        destination: "",
        imgUrl:
          "https://images.unsplash.com/photo-1562386130-926081dc4fbf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=600&q=60",
        type: "",
        start: "",
        end: "",
        likedBy: [],
        participantCap: "",
        meetingTime: "",
        meetingPoint: "",
        chat: { msgs: [] }
      }
    };
  },
  created() {
    const tripId = this.$route.params.id;
    if (tripId) {
      this.$store
        .dispatch({ type: "getById", tripId })
        .then(trip => (this.trip = trip));
    }
  },
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    }
  },
  methods: {
    async save(ev) {
      const file = ev.target[8].files[0];
      // loading.io
      if (file) {
        try {
          const url = await this.$store.dispatch({ type: "uploadImg", file });
          this.trip.imgUrl = url;
        } catch (err) {
          console.log(err);
          // swal
        }
      }

      try {
        const newTrip = await this.$store.dispatch({
          type: "save",
          trip: this.trip
        });
        this.loggedInUser.tripsOwner.push({
          tripId: newTrip._id,
        });
      } catch (err) {
        console.log(err);
      }

      try {
        await this.$store.dispatch({
          type: "updateUser",
          user: this.loggedInUser
        });
      } catch (err) {
        console.log(err);
      }
      this.$router.push("/");
    }
  },
  components: {
    AppHeader
  }
};
</script>


<style lang="scss" scoped>
input,
textarea,
button {
  background-color: white;
  padding: 5px;
  border: black 1px solid;
}
</style>
