<template>
  <section class="trip-filter">
    <form @submit.prevent="setFilter">
      <label>
        Destination
        <input type="text" v-model="filter.txt" @input="setFilter"/>
      </label>
      <label>
        From
        <input type="date" v-model="filter.from" @input="setFilter"/>
      </label>
      <label>
        To
        <input type="date" v-model="filter.to" @input="setFilter"/>
      </label>
      <select class="select" v-model="filter.type" @input="setFilter">
        <option value="culture">Culture</option>
        <option value="romantic">Romantic</option>
        <option value="history">History</option>
        <option value="culinary">Culinary</option>
      </select>
      <button>Search</button>
    </form>
  </section>
</template>


<script>
export default {
  data() {
    return {
      filter: {txt: '', from: '', to: '', type: ''}
    };
  },
  created(){

  },
  methods:{
      setFilter(){
          this.$emit('setFilter', this.filter)
      }
  }
};
</script>


<style lang="scss" scoped>
.select{
  background-color: white;
  padding: 10px;
}
input,
button {
  background-color: white;
  padding: 5px;
  border: black 1px solid;
}
</style>
