import TripService from '../Services/TripService.js'

export default {
    // strict: true,
    state: {
        trips: [],
        ownerTrips: []
    },
    mutations: {
        setTrips(state, { trips }) {
            state.trips = trips;
        },
        remove(state, { id }) {
            const idx = state.trips.findIndex(currTrip => currTrip._id === id)
            state.trips.splice(idx, 1)
        },
        update(state, { trip }) {

            var idx = state.trips.findIndex(cuurTrip => cuurTrip._id === trip._id)
            state.trips.splice(idx, 1, trip)
        },
        save(state, { trip }) {
            state.trips.push(trip)
        }
    },
    getters: {
        getTrips(state) {
            return state.trips;
        }
    },
    actions: {
        async loadTrips(context, {filter}) {
            try {
                const trips = await TripService.query(filter)
                context.commit({ type: 'setTrips', trips })

            } catch (err) {
                throw err;
            }

        },
        async  getById(context, { tripId }) {
            try {
                return await TripService.getById(tripId)
            }
            catch (err) {
                throw err;
            }
        },
        async remove(context, { id }) {
            try {
                const tripId = await TripService.remove(id)
                context.commit({ type: 'remove', id: tripId })
            } catch (err) {
                throw err;
            }
        },
        async save(context, { trip }) {
            if (trip._id) {
                try {
                    const tripToUpdate = await TripService.save(trip)
                    context.commit({ type: 'update', trip: tripToUpdate })
                } catch (err) {
                    throw err;
                }
            } else {
                try {
                    const newTrip = await TripService.save(trip)
                    context.commit({ type: 'save', trip: newTrip })
                    return newTrip
                } catch (err) {
                    throw err;
                }
            }
        },
        async uploadImg(context, { file }) {
            try {
                const url = await TripService.handleUploadImage(file)
                return url;
            } catch (err) {
                throw err;
            }
        }
    }
}