<template>
  <section v-if="user">
    <h2>user porfile</h2>
    <div>
      <img class="user-img" :src="user.imgUrl" />
    </div>
    <form @submit.prevent="save">
      <div>
        <input type="text" v-model="user.firstName" />
      </div>
      <div>
        <input type="text" v-model="user.lastName" />
      </div>
      <div>
        <textarea cols="30" rows="10" v-model="user.aboutMe" placeholder="About Me"></textarea>
      </div>
      <div>
        <textarea cols="30" rows="10" v-model="user.interests" placeholder="Interests"></textarea>
      </div>
      <input type="file" />
      <button>Save</button>
    </form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      user: null
    };
  },
  async created() {
    const userId = this.$route.params.id;
    const userToShow = await this.$store.dispatch({
      type: "getUserById",
      id: userId
    });
    this.user = userToShow;
  },
  methods: {
    async save(ev) {
      const file = ev.target[4].files[0];
      if (file) {
        try {
          const url = await this.$store.dispatch({ type: "uploadImg", file });
          this.user.imgUrl = url;
        } catch (err) {
          console.log(err);
          // swal
        }
      }
      try {
        await this.$store.dispatch({type: "updateUser",user: this.user});
        // const result = await this.$store.dispatch({type: "updateUser",user: this.user});
        this.$router.push("/");
      } catch (err) {
        console.log(err);
        // swal
      }
    }
  }
};
</script>

<style lang="scss" scoped>
input,
textarea {
  padding: 5px;
  font-size: 1.2rem;
  border-radius: 2px;
  border: 0.5px gray solid;
  color: black;
  background-color: whitesmoke;
}
button {
  background-color: white;
  padding: 5px;
  border: black 1px solid;
}
.user-img {
  width: 100px;
}
</style>



